#leafContainer{position:absolute;width:100%;height:100%;z-index:-1}#leafContainer>div{position:absolute;width:100px;height:100px;animation-iteration-count:infinite, infinite;animation-direction:normal, normal;animation-timing-function:linear, ease-in}#leafContainer>div>img{background-color:transparent;position:absolute;width:100px;height:100px;animation-iteration-count:infinite;animation-direction:alternate;animation-timing-function:ease-in-out;transform-origin:50% -100%}@keyframes fade{0%{opacity:1}95%{opacity:1}100%{opacity:0}}@keyframes drop{0%{transform:translate(0px, -50px)}100%{transform:translate(0px, 90vh)}}@keyframes clockwiseSpin{0%{transform:rotate(-50deg)}100%{transform:rotate(50deg)}}@keyframes counterclockwiseSpinAndFlip{0%{transform:scale(-1, 1) rotate(50deg)}100%{transform:scale(-1, 1) rotate(-50deg)}}
